}

.main-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #595959;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: justify;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.main-wrap .main-cont {
  position: relative;
  width: 100%;
  text-align: center;
}
.main-wrap .main-cont .main-input {
  display: inline-block;
  width: 70%;
  height: 40px;
  padding: 0 15px;
  border: 0;
  border-radius: 8px;
  outline: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.main-wrap .main-cont .main-alert {
  display: inline-block;
  position: absolute;
  bottom: -40px;
  left: 15%;
  width: 70%;
  height: 40px;
  line-height: 40px;
  color: #f5f5f5;
  text-align: left;
}

.control-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #262626;
}
.control-wrap .pos-control {
  position: absolute;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  top: 50%;
  margin-top: -60px;
  left: 10%;
  background-color: rgba(255, 255, 255, 0.1);
}
.control-wrap .pos-control .pos-btn {
  position: absolute;
  margin-left: 60px;
  margin-top: 60px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.3);
}
.control-wrap .pos-control .pos-btn:after {
  content: '';
  position: absolute;
  width: 2px;
  height: 2px;
  left: 50%;
  margin-left: -1px;
  top: 50%;
  margin-top: -1px;
  background-color: #fff;
}
.control-wrap .fire-control {
  position: absolute;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  top: 50%;
  margin-top: -40px;
  right: 10%;
  background-color: rgba(255, 255, 255, 0.1);
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 0.05s;
  transition: all 0.05s;
}
.control-wrap .fire-control img {
  position: relative;
  width: 40px;
  height: 40px;
  left: 20px;
  top: 20px;
}
.control-wrap .fire-control-active {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.mask-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: justify;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  z-index: 10;
}
.mask-wrap .mask-alert {
  display: inline-block;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 8px;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.mask-wrap .mask-alert-active {
  -webkit-animation: show 0.2s forwards;
          animation: show 0.2s forwards;
}
@-webkit-keyframes show {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
80% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes show {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
80% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}

.ruler-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.ruler-wrap .vertical-line {
  position: absolute;
  width: 1px;
  background-color: #f5f5f5;
  height: 100%;
}
.ruler-wrap .horizontal-line {
  position: absolute;
  width: 100%;
  background-color: #f5f5f5;
  height: 1px;
}

.stateBar {
  position: absolute;
  top: 0;
  left: 15%;
  width: 70%;
  height: 36px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 0 0 8px 8px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 0 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.stateBar .stateItem {
  width: 25%;
  font-size: 14px;
  line-height: 36px;
  color: #fff;
}
